<template>
  <div class="camera-system">
    <el-row :gutter="20">
      <el-col :span="8" class="camera-list">
        <el-card shadow="hover" style="height: 600px; overflow-y: auto;">
          <div class="header">
            <span></span>
            <el-button size="mini" type="primary" @click="openAddDialog"></el-button>
          </div>
          <el-menu
            :default-active="selectedCamera ? selectedCamera.id.toString() : ''"
            @select="selectCamera"
            class="menu"
            router
          >
            <el-menu-item
              v-for="camera in cameras"
              :key="camera.id"
              :index="camera.id.toString()"
            >
              {{ camera.name }}
              <el-button
                size="mini"
                type="danger"
                icon="el-icon-delete"
                circle
                @click.stop="removeCamera(camera.id)"
              />
            </el-menu-item>
          </el-menu>
        </el-card>
      </el-col>

      <el-col :span="16" class="camera-detail">
        <el-card shadow="hover" style="min-height: 600px;">
          <div v-if="selectedCamera">
            <h3>{{ selectedCamera.name }} </h3>
            <img
              :src="cameraImage"
              alt=""
              style="width: 100%; max-height: 300px; object-fit: contain; margin-bottom: 20px;"
            />
            <el-form :model="cameraConfig" label-width="100px">
              <el-form-item label="">
                <el-input v-model="cameraConfig.resolution" placeholder="" />
              </el-form-item>
              <el-form-item label=" (FPS)">
                <el-input v-model.number="cameraConfig.fps" placeholder="" type="number" />
              </el-form-item>
              <el-form-item label="">
                <el-input
                  type="textarea"
                  v-model="cameraConfig.remark"
                  placeholder=""
                />
              </el-form-item>
            </el-form>
            <el-button type="primary" @click="saveConfig"></el-button>
          </div>
          <div v-else>
            <p></p>
          </div>
        </el-card>
      </el-col>
    </el-row>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addDialogVisible"
      width="700px"
      @close="resetAddDialog"
    >
      <div style="position: relative; user-select: none;">
        <!--  -->
        <img
          ref="imageRef"
          :src="cameraImage"
          alt=""
          style="width: 100%; max-height: 400px; object-fit: contain; border: 1px solid #ccc;"
          @click="onImageClick"
        />
        <!-- SVG -->
        <svg
          v-if="points.length > 0"
          :style="{ position: 'absolute', top: 0, left: 0, width: imageSize.width + 'px', height: imageSize.height + 'px', pointerEvents: 'none' }"
        >
          <rect
            v-if="points.length === 2"
            :x="rectX"
            :y="rectY"
            :width="rectWidth"
            :height="rectHeight"
            stroke="red"
            stroke-width="2"
            fill="rgba(255, 0, 0, 0.3)"
          />
          <!--  -->
          <circle
            v-for="(p, i) in points"
            :key="i"
            :cx="p.x"
            :cy="p.y"
            r="5"
            fill="red"
            stroke="white"
            stroke-width="1"
          />
        </svg>
      </div>
      <div style="margin-top: 15px; text-align: right;">
        <el-button @click="resetPoints" size="small"></el-button>
        <el-button type="primary" :disabled="points.length !== 2" @click="confirmAddCamera"
          ></el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'CameraSystem',
  data() {
    return {
      cameras: [
        { id: 1, name: '', alertRegion: null },
        { id: 2, name: '', alertRegion: null },
      ],
      selectedCamera: null,
      cameraConfig: {
        resolution: '',
        fps: null,
        remark: '',
      },
      cameraImage: 'https://s2.loli.net/2022/12/20/EqxNvAYwcTI5Mkz.jpg',
      nextCameraId: 3,

      // 
      addDialogVisible: false,
      points: [], //  {x,y}
      imageSize: { width: 0, height: 0 }, // 
      newCameraName: '',
    }
  },
  computed: {
    // 
    rectX() {
      return Math.min(this.points[0]?.x ?? 0, this.points[1]?.x ?? 0)
    },
    rectY() {
      return Math.min(this.points[0]?.y ?? 0, this.points[1]?.y ?? 0)
    },
    rectWidth() {
      return Math.abs((this.points[1]?.x ?? 0) - (this.points[0]?.x ?? 0))
    },
    rectHeight() {
      return Math.abs((this.points[1]?.y ?? 0) - (this.points[0]?.y ?? 0))
    },
  },
  methods: {
    selectCamera(id) {
      this.selectedCamera = this.cameras.find((c) => c.id === Number(id))
      if (this.selectedCamera) {
        this.cameraConfig = {
          resolution: '1920x1080',
          fps: 30,
          remark: '',
        }
      }
    },
    openAddDialog() {
      this.addDialogVisible = true
      this.$nextTick(() => {
        const img = this.$refs.imageRef
        this.imageSize.width = img.clientWidth
        this.imageSize.height = img.clientHeight
      })
    },
    onImageClick(event) {
      if (this.points.length >= 2) return
      // 
      const rect = event.target.getBoundingClientRect()
      const x = event.clientX - rect.left
      const y = event.clientY - rect.top
      this.points.push({ x, y })
    },
    resetPoints() {
      this.points = []
    },
    confirmAddCamera() {
      if (this.points.length !== 2) {
        this.$message.warning('')
        return
      }
      const newName = prompt('', `${this.nextCameraId}`)
      if (!newName) {
        this.$message.warning('')
        return
      }
      // 
      this.cameras.push({
        id: this.nextCameraId,
        name: newName,
        alertRegion: {
          x: this.rectX,
          y: this.rectY,
          width: this.rectWidth,
          height: this.rectHeight,
        },
      })
      this.nextCameraId++
      this.$message.success('')
      this.addDialogVisible = false
      this.points = []
    },
    addCamera() {
      // 
      this.openAddDialog()
    },
    removeCamera(id) {
      this.$confirm('', '', {
        type: 'warning',
      })
        .then(() => {
          this.cameras = this.cameras.filter((c) => c.id !== id)
          if (this.selectedCamera && this.selectedCamera.id === id) {
            this.selectedCamera = null
          }
          this.$message.success('')
        })
        .catch(() => {
          this.$message.info('')
        })
    },
    saveConfig() {
      if (!this.selectedCamera) {
        this.$message.warning('')
        return
      }
      this.$message.success(`${this.selectedCamera.name}`)
      console.log(':', this.cameraConfig)
    },
  },
}
</script>

<style scoped>
.camera-system {
  padding: 20px;
}
.camera-list .header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  user-select: none;
}
.camera-list .menu {
  max-height: 520px;
  overflow-y: auto;
}
.camera-detail {
  background: #fff;
}
</style>
